import React, { Component } from 'react';

class Card extends Component {
    render(){
        return (
            <div style = {{backgroundColor : 'green', margin: '5px'}}>
                <h3> {this.props.id}</h3>
                <img src={`https://robohash.org/${this.props.username}?size=100x100`} alt="roboImage" />
                <p>{this.props.name}</p>
                <p>{this.props.email}</p> 
            </div>
        )
    }
}

// Card.defaultProps = {
//     myname : 'tope',
//     email : 'tlonge@gmail.com'
// }

export default Card;