import React, { Component, Fragment } from 'react'
import CardList from './CardList'
import SearchBox from './SearchBox';
import './App.css'
import Scroll from './Scroll'



class App extends Component{
    constructor(){
        super()

        this.state = {
            robots : [],
            searchtext : ''
        }
    }
    componentDidMount(){
        fetch('https://jsonplaceholder.typicode.com/users')
        .then(response=> response.json())
        .then(data=>{
            this.setState({robots: data})
        });
    }

    textInput =(event)=>{
        console.log(event.target.value)
        this.setState({searchtext: event.target.value})

        // this.setState({robots: filteredArray});      
    }
    

    render(){
        let filteredArray = this.state.robots.filter(profile=>{
            return profile.name.toLowerCase().includes(this.state.searchtext.toLowerCase());
        });
         if(this.state.robots.length === 0){
             return (
             <Fragment>

                <h1>ROBOFRIENDS APP</h1>
                <SearchBox text = {this.textInput}/>
                <h2>LOADING</h2>

             </Fragment>
             )
         }        
        else{
            return(
            
            <Fragment>
                <h1>ROBOFRIENDS APP</h1>
                <SearchBox text = {this.textInput}/>
                <Scroll>
                    <CardList robots = {filteredArray}/>
                </Scroll>
            </Fragment>
        )
        }
        
    }
}

export default App;