import React, { Component } from 'react';
import './CardList.css';
import Card from './Card'


class CardList extends Component {

render(){
  let result = this.props.robots.map((element, index)=>{
    return (<Card id = {element.id} name = {element.name} email = {element.email} key = {index} username = {element.username}/>)
  })
    return (
      <div className="App">
        {result}
      </div>
    );
}
}

export default CardList;
