import React, { Component, Fragment } from 'react';



class SearchBox extends Component {

    render() { 
        return ( 
            <Fragment>
                <input placeholder='Enter your search' type = 'search' onChange = {this.props.text}></input>
                {/* <p>{this.props.text}</p> */}
            </Fragment>
         );
    }
}
 
export default SearchBox;